@import url(https://fonts.googleapis.com/css?family=Lato);
html,
body {
  height: 100%;
}
*,
:after,
:before {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  width: 100%;
  font-weight: 500;
  font-family: 'Lato';
  overflow: hidden;
}
.container {
  width: 1024px;
  height: 100%;
  text-align: center;
  margin: 0 auto;
  position: relative;
}
.content {
  padding-top: 5em;
}
.content .title {
  font-size: 65px;
  font-weight: 800;
  margin-bottom: 50px;
  line-height: 1.1;
  letter-spacing: -1px;
}
.content .desc {
  font-size: 28px;
  font-weight: 400;
  margin: 2em 0;
}
.content a {
  font-size: 18px;
  color: #5FCF80;
  text-decoration: none;
}
.content a span {
  color: #C792E3;
}
.footer img {
  position: absolute;
  bottom: 0;
  max-width: 100%;
  display: block;
}
@media screen and (max-width: 1024px) {
  .container {
    width: 100%;
  }
  .content {
    padding-top: 2em;
  }
  .content .title {
    font-size: 60px;
    line-height: 3.5em;
  }
  .content .desc {
    font-size: 16px;
    margin-bottom: 2em;
  }
}
